                grid-column: span 2;
                grid-row: span 2; }

            .img2 {
                grid-column: span 2; }

            .img3 {
                grid-column: span 2;
                grid-row: span 2; }

            .img4 {
                grid-column: span 2;
                grid-row: span 2; }

            .img5 {
