@import "./sass-variables.sass";

#skills {
    width: 100%;
    // max-width: $maxWidth
    display: flex;
    flex-direction: column;
    padding-top: 250px;
    padding-bottom: 3rem;
    background: $lin3;
    z-index: 4;
    position: relative;
    align-items: center;
    #skillsTitles {
        width: 494px;
        text-align: left;
        margin-top: 4rem;
        text-align: center;
        h1 {
            // margin: 2rem
            color: $cthird; }
        h5 {
 } }            // margin: 0 2rem
    #skillsContainer {
        padding: 50px 0;
        width: 1200px;
        display: flex;
        justify-content: space-around; } }

@media (max-width: 1200px) {
    #skills {
        #imgSkillsContainer {
            display: grid;
            grid-template-columns: repeat(3, 188px);
            grid-template-rows: repeat(2, 226px);
            grid-gap: 0.5rem; } } }

@media (max-width: 1000px) {
    #skills {
        width: 90%;
        max-width: 1200px;
        margin: 0 auto;
        display: flex;
        flex-direction: column;
        #skillsTitles {
            margin: 0 auto;
            margin-top: 4rem;
            max-width: 100%; }
        #imgSkillsContainer {
            margin: 0 auto;
            display: grid;
            grid-template-columns: repeat(3, 228px);
            grid-template-rows: repeat(2, 274px);
            grid-gap: 0.5rem;
            .imageDiv {
                // width: 100%
                // margin: 1rem
 } } } }                // width: auto

@media (max-width: 650px) {
    #skills {
        width: 90%;
        max-width: 1200px;
        margin: 0 auto;
        display: flex;
        flex-direction: column;
        #skillsTitles {
            margin: 0 auto;
            max-width: 100%;
            #skillsButton {
                width: 100%;
                display: flex;
                justify-content: center; }
            button {
                margin: 1rem; } }
        #imgSkillsContainer {
            margin: 0 auto;
            display: grid;
            grid-template-columns: repeat(2, 40vw);
            grid-template-rows: repeat(3, 50vw);
            grid-gap: 0.5rem;
            .imageDiv {
                // width: 100%
                // margin: 1rem
 } } } }                // width: auto
